                  '\n<figure><img alt="" src="https://cdn-images-1.medium.com/max/1024/1*rpxl4-1_V2kyw4QAAts4gw.png"></figure><p>Hello! This is On-demand Layer 2 Platform Tokamak Network.</p>\n<p>We are happy to announce the winners of the 4th week of the NFT event that we announced in advance.</p>\n<h3><strong>Details of distribution</strong></h3>\n<p>Tokamak Network NFT Contract: 0x073afd0ca0b12da8543e67b664dbf9fcc03fca99</p>\n<p>Event2 tx: 43~48 <a href="https://etherscan.io/tx/0xb1afc7343eaa927c578c8541313bba79058b47edb8ee76a5d0af4a3412650401">https://etherscan.io/tx/0xb1afc7343eaa927c578c8541313bba79058b47edb8ee76a5d0af4a3412650401</a></p>\n<h3>Winner</h3>\n<h3>Event2: Stake TON and get NFT!</h3>\n<ol><li>Users who newly staked on Tokamak Network staking service after the event article posting: 6</li></ol>\n<ul>\n<li>0x90**************************************</li>\n<li>0x65**************************************</li>\n<li>0x44**************************************</li>\n<li>0xE0**************************************</li>\n<li>0xFf**************************************</li>\n<li>0x0F**************************************</li>\n</ul>\n<h3>Distribution Date: Every Friday</h3>\n<p>This NFT Event is ongoing, so please participate and pay attention.</p>\n<p>Thank you.</p>\n<p>안녕하세요. 온디맨드 레이어 2 플랫폼 ‘토카막 네트워크’입니다.</p>\n<p><a href="https://medium.com/onther-tech/tokamak-network-dao-beta-launch-celebration-nft-giveaway-a5e4242a651a">NFT 이벤트</a>의 4주 차 당첨자를 공지합니다.</p>\n<p>지급 내용은 아래의 TXID를 통해 확인이 가능합니다.</p>\n<h3><strong>지급 상세</strong></h3>\n<p>토카막 네트워크 NFT 컨트랙트 주소: 0x073afd0ca0b12da8543e67b664dbf9fcc03fca99</p>\n<p>Event2 tx: 43~48 <a href="https://etherscan.io/tx/0xb1afc7343eaa927c578c8541313bba79058b47edb8ee76a5d0af4a3412650401">https://etherscan.io/tx/0xb1afc7343eaa927c578c8541313bba79058b47edb8ee76a5d0af4a3412650401</a></p>\n<h3><strong>당첨자</strong></h3>\n<h3>Event2: Tokamak Network에 스테이킹하고 NFT 받자!</h3>\n<ol><li>Tokamak Network 스테이킹 서비스에 신규로 스테이킹한 유저: 6</li></ol>\n<ul>\n<li>0x90**************************************</li>\n<li>0x65**************************************</li>\n<li>0x44**************************************</li>\n<li>0xE0**************************************</li>\n<li>0xFf**************************************</li>\n<li>0x0F**************************************</li>\n</ul>\n<h3>지급일: 매주 금요일</h3>\n<p>이번 NFT Event는 계속 진행 중이오니 많은 참여와 관심 부탁드립니다.</p>\n<p>감사합니다.</p>\n<img src="https://medium.com/_/stat?event=post.clientViewed&amp;referrerSource=full_rss&amp;postId=f9407e0767b7" width="1" height="1" alt=""><hr>\n<p><a href="https://medium.com/onther-tech/announcement-of-nft-event-4th-week-winner-en-kr-f9407e0767b7">Announcement of NFT Event 4th week winner [EN/KR]</a> was originally published in <a href="https://medium.com/onther-tech">Onther</a> on Medium, where people are continuing the conversation by highlighting and responding to this story.</p>\n',
                content:
                  '\n<figure><img alt="" src="https://cdn-images-1.medium.com/max/1024/1*rpxl4-1_V2kyw4QAAts4gw.png"></figure><p>Hello! This is On-demand Layer 2 Platform Tokamak Network.</p>\n<p>We are happy to announce the winners of the 4th week of the NFT event that we announced in advance.</p>\n<h3><strong>Details of distribution</strong></h3>\n<p>Tokamak Network NFT Contract: 0x073afd0ca0b12da8543e67b664dbf9fcc03fca99</p>\n<p>Event2 tx: 43~48 <a href="https://etherscan.io/tx/0xb1afc7343eaa927c578c8541313bba79058b47edb8ee76a5d0af4a3412650401">https://etherscan.io/tx/0xb1afc7343eaa927c578c8541313bba79058b47edb8ee76a5d0af4a3412650401</a></p>\n<h3>Winner</h3>\n<h3>Event2: Stake TON and get NFT!</h3>\n<ol><li>Users who newly staked on Tokamak Network staking service after the event article posting: 6</li></ol>\n<ul>\n<li>0x90**************************************</li>\n<li>0x65**************************************</li>\n<li>0x44**************************************</li>\n<li>0xE0**************************************</li>\n<li>0xFf**************************************</li>\n<li>0x0F**************************************</li>\n</ul>\n<h3>Distribution Date: Every Friday</h3>\n<p>This NFT Event is ongoing, so please participate and pay attention.</p>\n<p>Thank you.</p>\n<p>안녕하세요. 온디맨드 레이어 2 플랫폼 ‘토카막 네트워크’입니다.</p>\n<p><a href="https://medium.com/onther-tech/tokamak-network-dao-beta-launch-celebration-nft-giveaway-a5e4242a651a">NFT 이벤트</a>의 4주 차 당첨자를 공지합니다.</p>\n<p>지급 내용은 아래의 TXID를 통해 확인이 가능합니다.</p>\n<h3><strong>지급 상세</strong></h3>\n<p>토카막 네트워크 NFT 컨트랙트 주소: 0x073afd0ca0b12da8543e67b664dbf9fcc03fca99</p>\n<p>Event2 tx: 43~48 <a href="https://etherscan.io/tx/0xb1afc7343eaa927c578c8541313bba79058b47edb8ee76a5d0af4a3412650401">https://etherscan.io/tx/0xb1afc7343eaa927c578c8541313bba79058b47edb8ee76a5d0af4a3412650401</a></p>\n<h3><strong>당첨자</strong></h3>\n<h3>Event2: Tokamak Network에 스테이킹하고 NFT 받자!</h3>\n<ol><li>Tokamak Network 스테이킹 서비스에 신규로 스테이킹한 유저: 6</li></ol>\n<ul>\n<li>0x90**************************************</li>\n<li>0x65**************************************</li>\n<li>0x44**************************************</li>\n<li>0xE0**************************************</li>\n<li>0xFf**************************************</li>\n<li>0x0F**************************************</li>\n</ul>\n<h3>지급일: 매주 금요일</h3>\n<p>이번 NFT Event는 계속 진행 중이오니 많은 참여와 관심 부탁드립니다.</p>\n<p>감사합니다.</p>\n<img src="https://medium.com/_/stat?event=post.clientViewed&amp;referrerSource=full_rss&amp;postId=f9407e0767b7" width="1" height="1" alt=""><hr>\n<p><a href="https://medium.com/onther-tech/announcement-of-nft-event-4th-week-winner-en-kr-f9407e0767b7">Announcement of NFT Event 4th week winner [EN/KR]</a> was originally published in <a href="https://medium.com/onther-tech">Onther</a> on Medium, where people are continuing the conversation by highlighting and responding to this story.</p>\n',
